function CourseViewPresent(props) {
  useEffect(() => {
    props.isLogin();

    if (!isEmpty(props.islogin)) {
      if (!props.islogin) {
        props.history.push("/signin");
      }
    }
  }, []);

  const [week, setWeek] = useState(courseStatus[0].courses[0].week);
  const [topic, setTopic] = useState(courseStatus[0].courses[0].topic);
  function videoId(link) {
    let video_id = link.split("v=")[1];
    let ampersandPosition = video_id.indexOf("&");
    if (ampersandPosition !== -1) {
      video_id = video_id.substring(0, ampersandPosition);
    }

    return video_id;
  }

  const getTitle = () => {
    let temp = detail[0].course[week].topics[topic];
    return `Topic ${temp.topicid} | ${temp.title}`;
  };
  const getBrief = () => {
    let temp = detail[0].course[week].topics[topic];
    return temp.topicbrief;
  };

  const getQuiz = () => {
    let temp = detail[0].course[week].topics[topic];
    if (temp.quiz !== []) {
      return temp.quiz;
    }
    return null;
  };

  const getVideo = () => {
    let temp = detail[0].course[week].topics[topic];
    let link = temp.link;
    let videoid = videoId(link);
    return videoid;
  };

  const getWeek = () => {
    return `(Week ${week + 1})`;
  };

  const goBackward = () => {
    if (week === 0 && topic === 0) {
      // for course started
      return null;
    } else if (week + 1 !== detail[0].course.length && topic === 0) {
      // for week started but topics length is greater than current topic
      setWeek(week - 1);
      setTopic(detail[0].course[week].topics.length - 1);
    } else if (week + 1 === detail[0].course.length && topic === 0) {
      // for week started but topics length is greater than current topic
      setWeek(week - 1);
      setTopic(detail[0].course[week].topics.length - 1);
    } else if (
      week + 1 === detail[0].course.length &&
      topic < detail[0].course[week].topics.length
    ) {
      // for week started but topics length is greater than current topic
      setTopic(topic - 1);
    } else if (
      week + 1 !== detail[0].course.length &&
      detail[0].course[week].topics.length !== topic + 1
    ) {
      setTopic(topic - 1);
    } else if (
      week + 1 !== detail[0].course.length &&
      detail[0].course[week].topics.length === topic + 1
    ) {
      setTopic(topic - 1);
    }
  };

  const goForward = () => {
    // console.log(course[week].topics.length)
    /* if course length is not equal to week and topic length is not eqaul to topics then increment in
     */
    if (
      week + 1 === detail[0].course.length &&
      detail[0].course[week].topics.length === topic + 1
    ) {
      // for course ended
      return null;
    } else if (
      week + 1 !== detail[0].course.length &&
      detail[0].course[week].topics.length === topic + 1
    ) {
      // for updating the week
      setTopic(0);
      setWeek(week + 1);
    } else if (
      week + 1 === detail[0].course.length &&
      detail[0].course[week].topics.length > topic + 1
    ) {
      // for last week but course has not ended
      setTopic(topic + 1);
    } else if (
      week + 1 !== detail[0].course.length &&
      detail[0].course[week].topics.length > topic + 1
    ) {
      // for week has not ended and topic are remaining
      setTopic(topic + 1);
    }
  };

  return (
    <DrawerView>
      <div className="mainContainer">
        <div className="row">
          <div className="col-sm-12 col-md-6 firstRowLeft">
            <h2 className="courseTitle">
              {_.toUpper(props.match.params.name)}
              <span className="pl-2 courseTitleSub">{getWeek()}</span>
            </h2>
          </div>
          <div className="col-sm-12 col-md-6 firstRowRight">
            <Link to="/" className="courseIconContainer">
              <FontAwesomeIcon icon={faBookOpen} className="p-2 courseIcons" />
            </Link>
            <Link to="/" className="ml-2 courseIconContainer mr-3">
              <FontAwesomeIcon icon={faQuestion} className="p-2 courseIcons" />
            </Link>
          </div>
        </div>

        <div className="row secondRow">
          <div className="col-sm-12 col-md-2 courseNavigationLeft">
            <button className="btn" onClick={goBackward}>
              <FontAwesomeIcon
                icon={faBackward}
                className="courseNavigationIcons"
              />{" "}
              Previous Topic
            </button>
          </div>
          <div className="col-sm-12 col-md-8 courseNavigationCenter">
            {/* Topic 14 | Choose Your Craft (Skills): What skills do you have? */}
            {getTitle()}
          </div>
          <div className="col-sm-12 col-md-2 courseNavigationRight">
            <button className="btn" onClick={goForward}>
              Next Topic{" "}
              <FontAwesomeIcon
                icon={faForward}
                className="courseNavigationIcons"
              />
            </button>
          </div>
        </div>

        <div className="mt-2 p-4">
          <nav>
            <div className="nav nav-tabs" id="nav-tab" role="tablist">
              <a
                className="nav-item nav-link active"
                id="nav-content-tab"
                data-toggle="tab"
                href="#nav-content"
                role="tab"
                aria-controls="nav-content"
                aria-selected="true"
              >
                Topic Content
              </a>
              <a
                className="nav-item nav-link"
                id="nav-discussion-tab"
                data-toggle="tab"
                href="#nav-discussion"
                role="tab"
                aria-controls="nav-discussion"
                aria-selected="false"
              >
                Topic Discussion
              </a>
            </div>
          </nav>
          <div className="tab-content navTopic" id="nav-tabContent">
            <div
              className="tab-pane fade show active p-4"
              id="nav-content"
              role="tabpanel"
              aria-labelledby="nav-content-tab"
            >
              <div
                className="video"
                style={{
                  position: "relative",
                  paddingBottom: "56.25%" /* 16:9 */,
                  paddingTop: 25,
                  height: 0,
                  border: "4px solid #031926"
                }}
              >
                <iframe
                  title="video"
                  style={{
                    position: "absolute",
                    top: 0,
                    left: 0,
                    width: "100%",
                    height: "100%"
                  }}
                  src={`https://www.youtube.com/embed/${getVideo()}`}
                  frameBorder="0"
                />
              </div>

              {/* Quiz if any */}
              <div className="paddingTop">
                <h6 className="font-weight-bold">Topic Brief</h6>
                <p>{getBrief()}</p>
                {getQuiz().map((data, index) => (
                  <div key={index}>
                    <h6 className="font-weight-bold">
                      Question: {data.quizid}
                    </h6>
                    <p className="quizQuestion">{data.quiztitle}</p>
                    <h6 className="font-weight-bold">
                      Answer Options (choose one):
                    </h6>
                    <div>
                      <input
                        type="radio"
                        name="quiz"
                        className="mr-4 quizRadio"
                      />
                      <label className="quizOption" htmlFor="quiz">
                        {data.optionA}
                      </label>
                    </div>
                    <div>
                      <input
                        type="radio"
                        name="quiz"
                        className="mr-4 quizRadio"
                      />
                      <label className="quizOption" htmlFor="quiz">
                        {data.optionB}
                      </label>
                    </div>
                    <div>
                      <input
                        type="radio"
                        name="quiz"
                        className="mr-4 quizRadio"
                      />
                      <label className="quizOption" htmlFor="quiz">
                        {data.optionC}
                      </label>
                    </div>
                    <div>
                      <input
                        type="radio"
                        name="quiz"
                        className="mr-4 quizRadio"
                      />
                      <label className="quizOption" htmlFor="quiz">
                        {data.optionD}
                      </label>
                    </div>
                  </div>
                ))}
              </div>
              {/* quiz end */}
            </div>
            <div
              className="tab-pane fade"
              id="nav-discussion"
              role="tabpanel"
              aria-labelledby="nav-discussion-tab"
            >
              discussion
            </div>
          </div>
        </div>
      </div>
    </DrawerView>
  );
}

const mapStateToProps = state => ({
  isloginuser: state.root.isloginuser
});
export default withRouter(
  connect(
    mapStateToProps,
    { isLogin }
  )(CourseViewPresent)
);
