import axios from "axios";
import { url, secretkey } from "../url";
import CryptoJS from "crypto-js";
import { decryptId, decryptUserId, decryptToken, logout } from "../utils";

export const isLogin = () => dispatch => {
  if (!decryptUserId() || !decryptToken() || !decryptId()) {
    dispatch({
      type: "IS_LOGIN_USER",
      payload: false
    });
  }

  axios
    .get(`${url}GetUser?Userid=${parseInt(decryptUserId())}`)
    .then(res => {
      if (
        decryptToken() === `${res.data[0].emailAddr}${res.data[0].Pwd}` &&
        parseInt(decryptId()) === parseInt(res.data[0].CompId) &&
        parseInt(decryptUserId()) === parseInt(res.data[0].userid)
      ) {
        dispatch({
          type: "IS_LOGIN_USER",
          payload: true
        });
      } else {
        logout();
      }
    })
    .catch(err =>
      dispatch({
        type: "IS_LOGIN_USER",
        payload: 404
      })
    );
};

