export const saveUser = (values, selectedDate, history) => dispatch => {
  const monthNames = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December"
  ];

  let dateofbirth = `${selectedDate.getDate()}-${
    monthNames[selectedDate.getMonth()]
  }-${selectedDate.getFullYear()}`;

  console.log(values, dateofbirth);
  axios
    .post(
      `SaveUser?firstname=Muhammad&middlename=Rizwan&lastname=Shaikh&EmailAddr=razzyshaikh@gmail.com&mailaddr=Krachi East&countryid=1&cityid=2&qualificationid=1&futurequalificationid=1&dob=10-Apr-1947&cnic=45652365236523&companyid=1&pwd=Hello@123&gender=M&UserType=U`
    )
    .then(res => {
      history.push("/signin");
      dispatch({
        type: "SAVE_USER",
        payload: res.data
      });
    })
    .catch(err => {
      dispatch({
        type: "GET_ERRORS",
        payload: err.response.status
      });
    });
};

export const updateUser = (values, history) => dispatch => {
  const monthNames = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December"
  ];

  let temp = new Date(values.dob);
  let dateofbirth = `${temp.getDate()}-${
    monthNames[temp.getMonth()]
  }-${temp.getFullYear()}`;
  console.log("=============>",values)
  axios
    .post(
      `${url}UpdateUser?UserId=${values.userid}&firstname=${values.firstname}&middlename=${values.middlename}&lastname=${values.lastname}&EmailAddr=${values.emailAddr}&mailaddr=${values.mailingaddress}&countryid=${values.countryid}&cityid=${values.cityid}&qualificationid=${values.qualificationid}&futurequalificationid=0&dob=${dateofbirth}&cnic=${values.cnic}&companyid=${decryptId()}&pwd=${values.Pwd}&gender=${values.gnder}&UserType=U&UserStatus=A`
    )
    .then(res => {
      window.location.reload();
      dispatch({
        type: "UPDATE_USER",
        payload: 200
      });
    })
    .catch(err => {
      dispatch({
        type: "UPDATE_USER",
        payload: 500
      });
    });
};

export const login = (values, history) => dispatch => {
  axios
    .get(`${url}Login?EmailAddress=${values.email}&UserPwd=${values.password}`)
    .then(res => {
      if (res.data[0].UserType !== "A") {
        let ciphertext = CryptoJS.AES.encrypt(
          `${res.data[0].emailAddr}${res.data[0].Pwd}`,
          secretkey
        );
        let userid = CryptoJS.AES.encrypt(`${res.data[0].userid}`, secretkey);
        let CompId = CryptoJS.AES.encrypt(`${res.data[0].CompId}`, secretkey);

        localStorage.setItem("token", ciphertext.toString());
        localStorage.setItem("userid", userid.toString());
        localStorage.setItem("CompId", CompId.toString());
        history.push("/");
        dispatch({
          type: "LOGIN",
          payload: res.data
        });
      } else {
        dispatch({
          type: "GET_ERRORS",
          payload: 404
        });
      }
    })
    .catch(res => {
      dispatch({
        type: "GET_ERRORS",
        payload: 404
      });
    });
};
